.top-bar {
	height: 67px;
	z-index: 51;
	display: flex;
	align-items: center;
	position: relative;
	border-bottom-width: 1px;
	border-color: #dee7ef
}

.app {
	background: #253c80;
	background-repeat: no-repeat;
	background-attachment: fixed;
	padding-top: 1.25rem;
	padding-bottom: 1.25rem
}

.login {
	position: relative
}

@media (max-width:1279px) {
	.login {
		background: linear-gradient(180deg, #253c80, #2b51b4);
		background-repeat: no-repeat;
		background-attachment: fixed
	}
}

.login:before {
	content: "";
	z-index: -1;
	margin-left: -48%;
	/* background-image: url(../images/bg-login-page.svg); */
	background-repeat: no-repeat;
	background-size: auto 100%;
	background-position: 100%;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0
}

@media (max-width:1279px) {
	.login:before {
		display: none
	}
}

.login .login__input {
	min-width: 350px;
	box-shadow: 0 3px 5px rgba(0, 0, 0, .03)
}

@media (max-width:1279px) {
	.login .login__input {
		min-width: 100%
	}
}

.mini-chat-box {
	width: 320px;
	height: 525px;
	margin-right: 360px
}

.mini-chat-box .mini-chat-box__text-box {
	max-width: 80%
}

.mini-chat-list {
	width: 320px;
	height: 570px
}

.mini-chat {
	width: 250px
}

.table {
	width: 100%;
	text-align: left
}

.table th {
	font-weight: 500
}

.table td,
.table th {
	padding: .75rem 1.25rem
}

.table.table--sm td,
.table.table--sm th {
	padding: .5rem 1rem
}

.table-report:not(.table-report--bordered) {
	border-spacing: 0 10px;
	border-collapse: separate
}

.table-report:not(.table-report--bordered) td {
	background-color: #fff;
	box-shadow: 20px 3px 20px rgba(0, 0, 0, .04)
}

.table-report:not(.table-report--bordered) td:first-child {
	border-top-left-radius: .375rem;
	border-bottom-left-radius: .375rem
}

.table-report:not(.table-report--bordered) td:last-child {
	border-top-right-radius: .375rem;
	border-bottom-right-radius: .375rem
}

.table-report:not(.table-report--bordered) td.table-report__action {
	padding-top: 0;
	padding-bottom: 0;
	position: relative
}

.table-report:not(.table-report--bordered) td.table-report__action:before {
	content: "";
	width: 1px;
	height: 2rem;
	background-color: #edf2f7;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	margin-top: auto;
	margin-bottom: auto
}

.table-report img {
	box-shadow: 0 0 0 2px #fff, 1px 1px 5px rgba(0, 0, 0, .32)
}

.report-timeline:before {
	content: "";
	height: 1000%;
	width: 1px;
	background-color: #edf2f7;
	position: absolute;
	margin-left: 1.25rem;
	margin-top: 1.25rem
}

.report-chart {
	position: relative
}

.report-chart:before {
	content: "";
	height: 93%;
	background: linear-gradient(90deg, #fff, hsla(0, 0%, 100%, .87), transparent);
	width: 3rem;
	left: 0;
	top: 0;
	margin-left: 2.5rem;
	position: absolute
}

@media (max-width:639px) {
	.report-chart:before {
		height: 85%
	}
}

.report-chart:after {
	content: "";
	height: 93%;
	background: linear-gradient(90deg, transparent, hsla(0, 0%, 100%, .87), #fff);
	width: 3rem;
	right: 0;
	top: 0;
	position: absolute;
	margin-right: .5rem
}

@media (max-width:639px) {
	.report-chart:after {
		height: 85%
	}
}

.search {
	position: relative
}

.search .search__input {
	width: 14rem;
	border-radius: 9999px;
	background-color: #d2dfea;
	padding-right: 2rem
}

.search .search__icon {
	width: 1.25rem;
	height: 1.25rem;
	position: absolute;
	margin-top: auto;
	margin-bottom: auto;
	top: 0;
	bottom: 0;
	margin-right: .75rem;
	right: 0;
	color: #365a74
}

.input {
	padding: .5rem .75rem;
	border-radius: .375rem;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none
}

.input:not(textarea).input--sm {
	padding: .25rem .5rem
}

.input:not(textarea).input--lg {
	padding: .75rem 1rem
}

.input:focus {
	outline: 0;
	box-shadow: 0 0 0 3px rgba(66, 153, 225, .5)
}

.input[type=radio] {
	width: 16px;
	height: 16px;
	position: relative;
	border-radius: 9999px;
	overflow: hidden;
	cursor: pointer
}

.input[type=radio]:before {
	content: "";
	width: 10px;
	height: 10px;
	transition: all .2s ease-in-out;
	position: absolute;
	border-radius: 9999px;
	background-color: #253c80;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	opacity: 0
}

.input[type=radio]:checked {
	border-color: #253c80
}

.input[type=radio]:checked:before {
	opacity: 1
}

.input[type=checkbox]:not(.input--switch) {
	width: 16px;
	height: 16px;
	border-radius: .2em;
	position: relative;
	overflow: hidden;
	cursor: pointer
}

.input[type=checkbox]:not(.input--switch):before {
	content: "";
	transition: all .2s ease-in-out;
	background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' fill='none' stroke='%23fff' stroke-width='3' stroke-linecap='round' stroke-linejoin='round' class='feather feather-check'%3E%3Cpath d='M20 6L9 17l-5-5'/%3E%3C/svg%3E");
	background-size: 80%;
	width: 100%;
	height: 100%;
	position: absolute;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	opacity: 0;
	background-repeat: no-repeat;
	background-position: 50%
}

.input[type=checkbox]:not(.input--switch):checked {
	background-color: #253c80;
	border-color: #253c80
}

.input[type=checkbox]:not(.input--switch):checked:before {
	opacity: 1
}

.input.input--switch[type=checkbox] {
	width: 38px;
	height: 24px;
	padding: 1px;
	outline: 0;
	border-radius: 9999px;
	position: relative;
	cursor: pointer
}

.input.input--switch[type=checkbox]:before {
	content: "";
	width: 22px;
	height: 22px;
	transition: all .2s ease-in-out;
	box-shadow: 1px 1px 3px rgba(0, 0, 0, .25);
	position: absolute;
	top: 0;
	bottom: 0;
	margin-top: auto;
	margin-bottom: auto;
	border-radius: 9999px
}

.input.input--switch[type=checkbox]:checked {
	background-color: #253c80;
	border-color: #253c80
}

.input.input--switch[type=checkbox]:checked:before {
	margin-left: 13px;
