	background-color: #fff;
	border-radius: .375rem
}

.box,
.report-box {
	position: relative
}

.report-box:before {
	content: "";
	width: 85%;
	background: #f9fafc;
	box-shadow: 0 3px 20px rgba(0, 0, 0, .04);
	height: 100%;
	margin-top: .75rem;
	position: absolute;
	border-radius: .375rem;
	margin-left: auto;
	margin-right: auto;
	right: 0;
	left: 0
}

.report-box .report-box__icon {
	width: 28px;
	height: 28px
}

.report-box .report-box__indicator {
	display: flex;
	border-radius: 9999px;
	color: #fff;
	font-size: .75rem;
	padding: 3px .25rem 3px .5rem;
	align-items: center;
	font-weight: 500
